import React, { useRef, useState } from 'react'
import emailjs from 'emailjs-com'

const Forms = () => {
    const [loading, setLoading] = useState(false)
    const Name = useRef()
    const Email = useRef()
    const Message = useRef()
    const handleEmail = (e) => {
        e.preventDefault()
        setLoading(true)

        const parametros = {
            Name: Name.current.value,
            Email: Email.current.value,
            Message: Message.current.value
        }

        emailjs.send('service_oar7d2q', 'template_1rlxfgg',parametros, 'user_Ux4XH62n4MdAC433r6DTT')
            .then(() => {
                console.log('correcto')
                setLoading(false)
            })
    }
    return (
        <div className="login-box" id="secc4">
            <h2>Contact</h2>
            <form onSubmit={handleEmail}>
                <div className="user-box">
                    <input type="text" name="Name" required="" ref={Name}></input>
                    <label>Name</label>
                </div>
                <div className="user-box">
                    <input type="text" name="Email" required="" ref={Email}></input>
                    <label>Email</label>
                </div>
                <div className="user-box">
                    <input type="text" name="Message" required="" ref={Message}></input>
                    <label>Message</label>
                </div>
                <a type="submit" onClick={ handleEmail} disable={loading}>
                    
                    <span></span>
                    <span></span>
                    <span></span>
                    <span></span>
                    Enviar
                </a>
            </form>
        </div>
    )
}

export default Forms
